 .wp-block-fuellogic-app-signup {
	//  font-size: 1em;
	//  background: #ffff001a;
	//  padding: 1em;
	//  position: relative; 

	 button{
		 background-color: var(--wp--preset--color--custom-green);
		 border-radius: 10px;
		 color: var(--wp--preset--color--black);
		 font-family: var(--wp--preset--font-family--system-font);
		 font-size: 22px;
		 font-weight: 600;
		 line-height: 1;
		 text-transform: uppercase;
		 padding: calc(0.667em + 2px) calc(1.333em + 2px);
		 border: none;
		 cursor: pointer;
		 position: relative;
			margin-top: 40px;
		 &:active{
			 top: 1px;
		 }
	 }
	 
	 form{
		 display: flex;
		 flex-direction: column;
		 gap: 20px;
 
		 input{
			 background: #323030;
			 border-radius: 12px;
			 border: none;
			 padding: 16px;
			 color: #fff;
			 font-size: 18px;
			 &:focus{
				 border: 2px solid #474747;
				 padding: 14px;
			 }
		 }
 
		 .remember{
			 display: flex;
			 justify-content: space-between;
			 gap: 10px;
			 color: #fff;
			 font-size: 18px;
			 input{
				 width: 20px;
				 height: 20px;
			 }
		 }
	 }
 }